<template>
    <div>
        <TopNav mdl="shopsetting" select="1"></TopNav>
        <div class="wyu-main">
            <div class="wyu-block">
                <div class="wyu-tips wyu-tips-info" style="margin-bottom:10px;">
                    <p class="tit"> 1）这里设置优惠券是否弹出以及弹出间隔等</p>
                </div>
                <div class="wyu-title">
                    <h2>弹出设置</h2>
                </div>
                <el-form ref="form" :model="form" label-width="120px">
                    <el-form-item label="是否弹出">
                        <el-switch v-model="form.is_open" :active-value="1" :inactive-value="0"></el-switch>
                    </el-form-item>
                    <el-form-item label="usinessid" >
                        <el-input v-model="form.usinessid" style="width: 500px" placeholder="请输入快递鸟usinessid"></el-input>
                    </el-form-item>
                    <el-form-item label="appkey" >
                        <el-input v-model="form.appkey" style="width: 500px" placeholder="请输入快递鸟appkey"></el-input>
                    </el-form-item>

                    <el-form-item >
                        <el-button size="small" type="primary"  @click="setKdNiao()">立即保存</el-button>
                        <el-button size="small">取消</el-button>
                    </el-form-item>

                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";

    export default {
        name: 'kdniaoSetting',
        components: {TopNav},
        data() {
            return {
                form: {
                    is_open: 0,
                    usinessid: '',
                    appkey: '',
                }
            }
        },

        created() {
            console.log(this.$store.getters.appToken);
            this.getKdNiao();
        },

        methods: {
            getKdNiao(){
                this.loading = true;
                api.post("/shop/admin.setting/getKdNiao", {
                    appToken: this.$store.getters.appToken,
                }).then(res => {
                    this.form = res;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },

            setKdNiao() {
                this.loading = true;
                api.post("/shop/admin.setting/setKdNiao", {
                    appToken: this.$store.getters.appToken,
                    is_open: this.form.is_open,
                    usinessid: this.form.usinessid,
                    appkey: this.form.appkey,
                }).then(res => {
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },


        }
    }
</script>
